"use client";

import { useRouter } from "next/navigation";

interface ExamButtonProps {
  isDisabled: boolean;
  href: string;
  children: React.ReactNode;
}

const ExamButton: React.FC<ExamButtonProps> = ({ isDisabled, href, children }) => {
  const router = useRouter();

  const handleClick = () => {
    if (!isDisabled) {
      router.push(href);
    }
  };

  console.log("Bot√≥n renderizado con texto:", children);

  return (
    <button
      disabled={isDisabled}
      onClick={handleClick}
      className="button--primary"
    >
      {children || "Bot√≥n vac√≠o"}
    </button>
  );
  
};

export default ExamButton;
